"use client"
import Input from "@/app/components/ui/Input"
import Button from "@/app/components/ui/button"
import React, {useRef} from "react"


export default function Signup() {
    const usernameRef = useRef<HTMLInputElement>(null);
    const passwordRef = useRef<HTMLInputElement>(null);
    return <div className=" flex justify-center w-full h-screen bg-gradient-to-r from-indigo-100 via-purple-100 to-pink-100 ">
        <div className=" flex flex-col space-y-5 p-40 bg-white/50 backdrop-blur-sm border-2 border-white/100 m-20">
             <Input reference={usernameRef} placeholder="email"/>
             <Input reference={passwordRef}  placeholder="password"/>
             <Button width="bold" title={'Signup'} color="purple" size="sm" />

        </div>


    </div>
}